import { Component } from '@angular/core';

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})
export class Header {
    title: string;
    constructor() {
        this.title = 'Welcome, please log in or register';
    }
}


